import { useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
// import { Redirect } from 'react-router-dom';


const SignupForm = () => {
    

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [firstname, setFirst] = useState('');
  const [lastname, setLast] = useState('');
  const [error, setError] = useState('');

  // function to handle the submit
  const handleSubmit = async (e) => {
      e.preventDefault();

      //header for authentication
      const authObject = {'Private-Key': 'b771620a-fb9d-4b48-a958-8c80e6c1e35a'}
      
      // post request to create user
      try {
          await axios.post(
            "https://api.chatengine.io/users/",
            {'username': username, 'secret': password}, // Body object
            {'headers': authObject} // Headers object
          );
          // login the user
          localStorage.setItem('username', username);
          localStorage.setItem('password', password);

          window.location.reload();
          setError('');
       } catch (error) {
           console.log(error)
           setError('Incorrect credentials, try again')
       }
  }      
  
  return(

    <div className="wrapper">
      <div className="form">
        <h1 className="title">
          Juice Chat
        </h1>
        <form onSubmit={handleSubmit}>
          <input type="text" value={firstname} onChange={(e) => setFirst(e.target.value)} className="input" placeholder="First Name" required />
          <input type="text" value={lastname} onChange={(e) => setLast(e.target.value)} className="input" placeholder="Last Name" required />
          <input type="text" value={username} onChange={(e) => setUsername(e.target.value)} className="input" placeholder="Username" required />
          <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} className="input" placeholder="Password" required />
          <div align="center">
            <button type="Submit" className="button">
              <span>Sign Up</span>
            </button>
          </div>
          <h2 className="error" align="center" style={{ color: 'white' }}>{error}</h2>
          <div align="center">
          <p style={{ color: 'white' }}>Don't have an account?</p>
          <Link to="/login">Login</Link>
        </div>
        </form>
      </div>
    </div>
  )
    
}

export default SignupForm;